  padding: 0;
  margin-bottom: 28px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  font-weight: normal;
  font-size: 36px;
  line-height: 60px; }

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 auto; }

.advantages__item {
  display: flex;
  min-height: 60px;
  position: relative;
  padding-left: 80px;
  margin-bottom: 20px; }
  .advantages__item::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: #68b738;
    background-position: 5px 5px;
    background-repeat: no-repeat;
    background-size: 50px; }
  .advantages__item:last-child {
    margin-bottom: 0; }

.advantages__item--nutritive::before {
  background-image: url("../img/advantage-leaf@1x.png"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .advantages__item--nutritive::before {
      background-image: url("../img/advantage-leaf@2x.png"); } }

.advantages__item--powder::before {
  background-image: url("../img/advantage-powder@1x.png"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .advantages__item--powder::before {
      background-image: url("../img/advantage-powder@2x.png"); } }

.advantages__item--ingestion::before {
  background-image: url("../img/advantage-dining@1x.png"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .advantages__item--ingestion::before {
      background-image: url("../img/advantage-dining@2x.png"); } }

.advantages__item--time::before {
  background-image: url("../img/advantage-clock@1x.png"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .advantages__item--time::before {
      background-image: url("../img/advantage-clock@2x.png"); } }

.advantages__descriptions {
  align-self: center;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444; }

@media (min-width: 768px) {
  .advantages {
    margin-bottom: 42px;
    padding-bottom: 37px;
    border-bottom: 1px solid #e6e6e6; }
  .advantages__title {
    margin-bottom: 72px;
    font-size: 60px;
    line-height: 60px; }
  .advantages__list {
