      font-size: 281px;
      line-height: 281px;
      color: #f2f2f2;
      z-index: 0; }
    .advantages__item:last-child, .advantages__item:nth-last-child(2) {
      margin-bottom: 0; }
  .advantages__item--nutritive::after {
    content: "1";
    left: 117px; }
  .advantages__item--powder::after {
    content: "2"; }
  .advantages__item--ingestion::after {
    content: "3"; }
  .advantages__item--time::after {
    content: "4"; }
  .advantages__descriptions {
    position: relative;
    width: 245px;
    margin: 0;
    padding: 0;
    padding-top: 110px;
